@mixin mq($type) {
  $phone: 480px;
  $tablet: 768px;
  $desktop: 1200px;

  @if $type == 'phone' {
    @media screen and (min-width: $phone) {
      @content;
    }
  }

  @if $type == 'phone-only' {
    @media screen and (max-width: #{$tablet - 0.02}) {
      @content;
    }
  }

  @if $type == 'tablet' {
    @media screen and (min-width: $tablet) {
      @content;
    }
  }

  @if $type == 'tablet-only' {
    @media screen and (max-width: #{$desktop - 0.02}) {
      @content;
    }
  }

  @if $type == 'desktop' {
    @media screen and (min-width: $desktop) {
      @content;
    }
  }
}
